
body{
  background-image: url(/react-demo/build/static/media/do-it-min.3fe8ed43.jpg);
  background-size: cover; 
}
*{
  letter-spacing: .25px;
}
h1{
  font-family: "Arial","Microsoft YaHei","\9ED1\4F53","\5B8B\4F53",sans-serif;
}

button{
  border-radius: 2px;
}
#root{
 
  display: flex;
  justify-content: center;
  min-height: 100vh;
}
.App {
  text-align: center;
  color: rgb(107, 107, 122);
  border: 1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em rgba(243, 241, 241, 0.15);
  position: relative;
}
.App > .todoList> li:nth-child(odd){
  background: #d3d3d3;
}
.App > .todoList> li:nth-child(even){
  background: transparent;
}

.App > h1 > button{
  text-align: center;
  text-decoration: none;
  box-shadow: inset 1px 1px 0 rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.07);
  background: #5689db;
  width: 100px;
  height: 28px;
  border-radius: 2px;
  color: white;
  font-size: 14px;
  border: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  margin: auto;
  right: 10px;
  top: 27px;
  cursor: pointer;
  font-family: “Arial”,”Microsoft YaHei”,”黑体”,”宋体”,sans-serif;
  padding: 2px;
  position: absolute;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  *::after{
    box-sizing: border-box;
  }
  *::before{
    box-sizing: border-box;
  }
  ul,ol,dl{
    list-style:none;
  }
  a{
    color: inherit;
  }
.TodoInput{
    width: 100%;
    line-height: 1.8;
    margin-bottom: 1em;
    font-size: 16px;
    font-family: “Arial”,”Microsoft YaHei”,”黑体”,”宋体”,sans-serif;
    padding: 0 .2em;
  }
.TodoItem{
    width: 100%;
    line-height: 1.8;
    margin-bottom: 1em;
    font-size: 16px;
    font-family: “Arial”,”Microsoft YaHei”,”黑体”,”宋体”;
    padding: 0 .2em;
    position: relative;
}

.TodoItem button{
    width: 90px;
    height: 100%;
    position: absolute;
    font-family: “Arial”; 
    line-height: 1;
    padding-left: 1em; 
    padding-right: 1em;
    background: #5689db;
    color: white;
    border-radius: 2px;
    box-shadow: inset 1px 1px 0 rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.07);
    border: none;
    font-size: 12px;
    margin-left: 10em;
}
.UserDialog-Wrapper{ 
    /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
    /*内容居中*/  display: flex; justify-content: center; align-items: center;
    /*给 dialog 添加遮罩效果*/background: hsla(0,0%,0%,0.40); 
    flex-direction: column;
    box-shadow: 1px 0px 0px rgba(0, 0, 0, 0.3) inset;

  }
  .UserDialog{
    display: flex;
    border: 1px solid;
    border-radius: 4px;
    min-width: 20em;
    background: white;
    border-top: 3px solid white;
    margin-top: -3px;
    
}
.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em;}
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;}
.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }
.UserDialog .forgotPassword > h3 { padding: 1em; }
.UserDialog .forgotPassword > form { padding: 0 1em 1em; }
 .UserDialog form>.row{ display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; }
.UserDialog form>.row.actions{ padding-left: 4em; display: flex; align-items: center;}
.UserDialog form>.row.actions button{ margin-right: .5em; }
 .UserDialog button{font: inherit; padding: .2em;}

 .UserDialog .logo{
   flex-direction: column;
   flex: 1 1;
   height: 100%;
   width: 100%;

 }
